}
a {
  color: #000;
}
.marBot {
  margin-bottom: 23px;
}
.marGinz {
  margin-top: 8px;
  margin-right: 8px;
}
.peachy {
  background: linear-gradient(40deg,#ffbc58,#ff3030)!important
